/* .tabResourceSelector .MuiListItemText-root-157:not(:last-of-type) { text-align:right; } */
/* .tabResourceSelector .MuiListItemText-root-157 + .MuiListItemText-root-157 { text-align:left; } */
.tabResourceSelector .individualView .prefLabel { -ms-flex: 10 1 auto ; flex: 10 1 auto ; margin-left:10px; }
.tabResourceSelector .individualView ul li > :first-child { -ms-flex: 1 10 auto ; flex: 1 10 auto ;  text-align:left; }

/* .prefLabel  */
.literalView { line-height:1.1 !important; }

.inDivHeading { display:-ms-flexbox; display:flex; -ms-flex-align: center; align-items: center; -ms-flex-pack: justify; justify-content: space-between; width:100%; }
.inDivHeading > div:last-child { margin:0 15px; font-size:20px; text-align:right; }

.IndividualHeading h1 { margin-top:0 !important; }


/* .individualView { flex:none !important; } */
.individualView ul ul li { -ms-flex-positive:1; flex-grow:1; } 
.individualView ul ul > div { display:-ms-flexbox; display:flex; }
li.noPad { padding-right:0; }

/* LiteralView */
/* .literalView { flex:none !important; } */
.literalView > div:first-child:last-child { width:calc(100% - 20px); }


.plusBut,.remoBut { position:relative !important; margin-top:10px !important;display:-ms-flexbox;display:flex;-ms-flex-align:top;align-items:top;right:0 !important;} 
/* .remoBut.ind { margin-right:24px; } */

.Pane2 .individualView > ul, .inCollapse > div > div > div { display:-ms-flexbox; display:flex; }
.Pane2 .individualView > ul > li, .inCollapse > div  { -ms-flex-positive:1; flex-grow:1; }


.RDFCompo > ul > li { padding:0; }
.RDFCompo > ul > li > div > div { padding:12px 16px 12px 16px !important; }


